@charset "UTF-8";

@import "import/lib-fonts";
@import "base";
@import "import/typography";
@import "import/reset-css";

/* clearfix */
.clearfix{
	*zoom:1;
	&:before, &:after{
		content:'';
		display:block;
	}
	&:after{
		clear:both;
	}
}

/* blind */
.blind {
    display: none;
    overflow: hidden;
    border: 0;
    position: relative;
    z-index: -1;
    width: 1px;
    height: 1px;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
}

body.popView{
    overflow: hidden;
    height: 100vh;
}

img {
    width: 100%;
}

input[type="file"]{
    display: none;
}

input::placeholder, textarea::placeholder{
    font-size: inherit;
    color: #aaa;
}

.f-right{float: right;}
.f-left{float: left;}

.po-r{position: relative;}
.po-a{position: absolute;}

.ov-h{overflow: hidden;}

.al-c{text-align: center !important;}
.al-l{text-align: left !important;}
.al-r{text-align: right !important;}

.line_01{overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

.pc_only{display: block;}
.mo_only{display: none;}

.c-red{color: #ff0000 !important;}

.bg-brown{background-color: #ddcbb9;}

.flex-end{@include flex('', 'flex-end !important');}
.flex-center{@include flex('', 'center !important');}
.flex-start{@include flex('', 'flex-start !important');}

.bold{font-weight: 700 !important;}

/* space */
$position: (t:top, b:bottom, r:right, l: left);
$total: 10;

@each $abbr, $pos in $position {
  @for $i from 0 to $total {
    .m#{$abbr}-#{$i} {margin-#{$pos}: $i*10px !important;}
    .p#{$abbr}-#{$i} {padding-#{$pos}: $i*10px !important;}
  }
}

@media all and (max-width:750px){
  .pc_only{display: none;}
  .mo_only{display: block;}

    /* space */
    $position: (t:top, b:bottom, r:right, l: left);
    $total: 10;

    @each $abbr, $pos in $position {
      @for $i from 0 to $total {
          .m#{$abbr}-#{$i} {margin-#{$pos}: $i*1.39vw !important;}
          .p#{$abbr}-#{$i} {padding-#{$pos}: $i*1.39vw !important;}
      }
    }
}

/* header */
.header{position: fixed; @include flex('space-between'); padding: 0 40px; width: 100%; height: 70px; background: #000; box-sizing: border-box; z-index: 10;}
.header .logo a{display: block; width: 91px; height: 43px;}
.header .util-btn .ico-menu a{display: none;}
.header .util-btn .search{display: flex; width: 150px; border-bottom: 1px solid #fff;}
.header .util-btn .search label{display: block; padding-bottom: 5px; width: 100%; color: #fff;}
.header .util-btn .search label input{width: 100%;}
.header .util-btn .search label input::placeholder{opacity: 0.5;}
.header .util-btn .search button{flex-shrink: 0; width: 20px; height: 20px; @include bg('/common/ico-search-w.png');}

.header .nav{height: 100%;}
.header .nav.pc_only{display: flex;}
.header .nav.mo_only{display: none;}
.header .nav .menu{@include flex(); font-family: $Libre; height: 100%;}
.header .nav .menu > li{display: flex; align-items: center; justify-content: center; position: relative; width: 120px; height: 100%; color: #fff; cursor: pointer;}
.header .nav .menu h2{text-transform: uppercase; font-size: 14px; opacity: 0.7;}
.header .nav .sub-menu-list{position: absolute; left: 0; top: 70px; background-color: #fff;}
.header .nav .sub-menu{display: none; position: absolute; padding-top: 20px; width: 100%; height: 160px; top: 70px; text-align: center; left: 50%; transform: translateX(-50%); font-size: 12px; color: #444;}
.header .nav .sub-menu > li a{display: inline-block;}
.header .nav .sub-menu > li + li{margin-top: 10px;}
.header .nav.on .sub-menu{display: block;}
.header .nav.on .menu li:hover h2{opacity: 1;}
.header .nav .menu::before{content: ''; display: none; position: absolute; left: 0; top: 70px; width: 100%; height: 160px; background-color: #e5e7eb;}
.header .nav.on .menu::before{display: block;}

/* footer */
.footer{padding: 50px 0; font-family: 'LibreBaskerville', 'SUIT', sans-serif; font-size: 14px; text-align: center; color: white; background: #000;}
.footer .company-info{@include flex('center'); flex-wrap: wrap; margin: 0 auto; width: 55%;}
.footer .company-info li{margin-bottom: 6px;}
.footer .company-info li + li{margin-left: 20px;}
.footer .policy-link{margin: 14px 0; @include flex('center');}
.footer .policy-link li + li{margin-left: 12px;}
.footer .copyright{font-weight: 700;}

/* visual */
.wrap{display: none;}
.contents{padding-top: 70px;}

.sec-kv{margin-bottom: 110px; height: 230px;}
.sec-kv .img-con{overflow: hidden; position: relative; height: 100%;}
.sec-kv .img-con img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: auto; height: 100%;}

/* page-tit */
.page-tit{margin-bottom: 70px; text-align: center;}
.page-tit h2{margin-bottom: 30px; font-family: $myeongjo; font-size: 36px; font-weight: 800; color: #212121;}
.page-tit .desc{font-size: 16px; font-weight: 500;}

.sub-tit h3{margin-bottom: 12px; font-family: $myeongjo; font-size: 28px; font-weight: 800;}
.sub-tit .en{font-family: $Libre; font-weight: 400;}
.sub-tit .desc{font-size: 18px; word-break: keep-all;}
.sub-tit .desc li + li{margin-top: 20px;}
.sub-tit .tit-l{margin-bottom: 50px; font-size: 58px; font-weight: 500;}
.sub-tit .tit-s{margin-bottom: 20px; font-size: 29px; font-weight: 700;}

.txt-con .tit{font-family: $Libre; font-size: 75px;}
.txt-con .desc{font-size: 18px; word-break: keep-all;}
.txt-con .tit small{font-family: $myeongjo; font-size: 29px; font-weight: 700;}

.inner{margin: 0 auto; width: 1024px;}
.inner-l{margin: 0 auto; max-width: 1920px; padding: 0 177px;}
.inner-l.pd-m{padding: 0 112px 0 177px;}
.inner-l.pd-s{padding: 0 60px 0 177px;}

/* btn */
.btn{display: inline-flex; align-items: center; justify-content: center; }
.btn-s{padding: 0 25px; height: 40px; font-size: 14px;}
.btn-m{min-width: 330px; height: 60px; font-size: 16px;}
.btn-l{min-width: 154px; padding: 20px 60px; border-radius: 10px; font-size: 18px;}
.btn.bg{color: #6b503c; background: #ddcbb9;}
.btn.line{border: 1px solid #575757;}

/* motion */
.motion{opacity: 0; transform: translateY(10%); transition: opacity 0.5s, transform 0.7s;}
.motion.onTrans{opacity: 1; transform: translateY(0);}

.motion.opacity{opacity: 0; transform: translateY(0); transition: opacity 0.7s;}
.motion.opacity.onTrans{opacity: 1;}
.motion.pos{transform: translateY(40%); transition: opacity 0.5s, transform 0.7s;}
.motion.pos.onTrans{transform: translateY(0);}

.motion{
  $total: 10;
  @for $i from 1 to $total {
    &.delay-#{$i}{transition-delay: $i*0.1s;}
  }
}


@media (max-width: 1440px) {
  .inner-l{padding: 0 100px;}
  .inner-l.pd-m{padding: 0 80px 0 100px;}
  .inner-l.pd-s{padding: 0 60px 0 100px;}
}

@media (max-width: 1280px) {
  .inner{padding: 0 80px; width: 100%;}
  .inner-l, .inner-l.pd-m, .inner-l.pd-s{padding: 0 60px;}
  .header .util-btn .search{display: none;}
}

@media (max-width: 980px) {
  .header .nav.pc_only{display: none;}
  .header .nav.mo_only{display: flex;}
  .header .util-btn{display: flex;}
  .header .util-btn .ico-menu a{display: inline-block; margin-left: 10px; width: 44px; height: 44px;}
  .header .nav{position: fixed; top: 0; left: -100%; overflow: hidden auto; padding: 0 0 60px 60px; width: 71%; height: 100%; background: #fff; z-index: 10; transition: left 0.5s;}
  .header .nav .menu{margin-top: 150px; flex-wrap: wrap; height: auto;}
  .header .nav .menu > li{display: block; margin-bottom: 30px; padding: 20px 0; width: 100%; height: auto; color: #212121;}
  .header .nav .menu > li + li{margin-left: 0;}
  .header .nav .menu h2{position: relative; font-size: 40px; opacity: 1; cursor: pointer;}
  .header .nav .menu h2::after{content: ''; position: absolute; top: 50%; right: 50px; transform: translateY(-50%); display: inline-block; width: 32px; height: 32px; @include bg('/common/ico-arrow-down2.png');}
  .header .nav .sub{position: absolute; left: 0; top: 70px; width: 100%; background-color: #fff;}
  .header .nav .sub-menu{display: none; position: static; margin: 60px 0 40px; height: auto; padding-top: 0; text-align: left; transform: translateX(0); font-size: 32px; color: #979797;}
  .header .nav .sub-menu > li + li{margin-top: 20px;}
  .header .nav .ico-close.mo_only{position:absolute; top: 35px; right: 30px; display: block; width: 65px; height: 65px; @include bg('common/ico-close2.png'); cursor: pointer;}

  .header.on::before{content: ''; position: fixed; top: 0; left: 0; right: 0; bottom: 0; display: block; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.5);}
  .header.on .nav{left: 0;}
  .header.on .nav .menu::before{display: none;}
  .header .nav .menu > li.on h2{border-bottom: 1px solid #212121;}
  .header .nav .menu > li.on h2::after{background-image: url("/assets/img/common/ico-arrow-up2.png")}

  .inner{padding: 0 40px;}
}

@media (max-width: 750px) {
  .sec-kv{margin-bottom: 14.67vw; height: 30.67vw;}

  .page-tit{margin-bottom: 9.33vw;}
  .page-tit h2{margin-bottom: 4.00vw; font-size: 6.13vw;}
  .page-tit .desc{font-size: 3.20vw;}

  .sub-tit h3{margin-bottom: 2.93vw; font-size: 5.27vw;}
  .sub-tit .desc{font-size: 3.73vw;}
  .sub-tit .desc li + li{margin-top: 5.33vw;}
  .sub-tit .tit-l{margin-bottom: 6.67vw; font-size: 7.73vw;}
  .sub-tit .tit-s{margin-bottom: 4.00vw; font-size: 4.27vw;}

  .txt-con .tit{font-size: 10.00vw;}
  .txt-con .desc{font-size: 3.73vw;}
  .txt-con .tit small{font-size: 4.27vw;}

  .btn-l{padding: 2.67vw 8.00vw; width: 100%; border-radius: 2.67vw; font-size: 3.73vw;}
  .btn-s{padding: 0 5.33vw; height: 8.00vw; font-size: 3.20vw;}
  .btn-m{height: 9.33vw; font-size: 3.20vw;}
  .btn.line{border-width: 0.27vw;}

  .inner{padding: 0 5.33vw;}
  .inner-l, .inner-l.pd-m, .inner-l.pd-s{padding: 0 5.33vw;}

  /* header */
  .header .nav.pc_only{display: none;}
  .header .nav.mo_only{display: block;}

  .header .util-btn .ico-menu a{margin-left: 1.33vw; width: 5.87vw; height: 5.87vw;}
  .header .nav{padding: 0 0 8.00vw 8.00vw;}
  .header .nav .menu{margin-top: 20.00vw;}
  .header .nav .menu > li{margin-bottom: 4.00vw; padding: 2.67vw 0;}
  .header .nav .menu h2{font-size: 5.33vw;}
  .header .nav .menu h2::after{right: 6.67vw; width: 4.27vw; height: 4.27vw;}
  .header .nav .sub{top: 9.33vw;}
  .header .nav .sub-menu{margin: 8.00vw 0 5.33vw; font-size: 4.27vw;}
  .header .nav .sub-menu > li + li{margin-top: 2.67vw;}
  .header .nav .ico-close.mo_only{top: 4.67vw; right: 4.00vw; width: 8.67vw; height: 8.67vw;}

  .header.on::before{content: ''; position: fixed; top: 0; left: 0; right: 0; bottom: 0; display: block; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.5);}
  .header.on .nav{left: 0;}
  .header.on .nav .menu::before{display: none;}
  .header .nav .menu > li.on h2{border-bottom: 0.13vw solid #212121;}
  .header .nav .menu > li.on h2::after{background-image: url("/assets/img/common/ico-arrow-up2.png")}

  /* footer */
  .footer{padding: 6.67vw 0; font-size: 2.67vw;}
  .footer .company-info{width: 100%;}
  .footer .company-info li{margin-bottom: 2.13vw;}
  .footer .company-info li + li{margin-left: 2.67vw;}
  .footer .policy-link{margin: 3.20vw 0; }
  .footer .policy-link li + li{margin-left: 2.67vw;}

}

