/* header */
.header {
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 40px;
	width: 100%;
	height: 70px;
	background: #000;
	box-sizing: border-box;
	z-index: 10;
}

.header .logo a {
	display: block;
	width: 91px;
	height: 43px;
}

.header .util-btn .ico-menu a {
	display: none;
}

.header .util-btn .search {
	display: flex;
	width: 150px;
	border-bottom: 1px solid #fff;
}

.header .util-btn .search label {
	display: block;
	padding-bottom: 5px;
	width: 100%;
	color: #fff;
}

.header .util-btn .search label input {
	width: 100%;
}

.header .util-btn .search label input::placeholder {
	opacity: 0.5;
}

.header .util-btn .search button {
	flex-shrink: 0;
	width: 20px;
	height: 20px;
	background: url("/assets/img//common/ico-search-w.png") no-repeat center/100% auto;
}

.header .nav {
	height: 100%;
}

.header .nav.pc_only {
	display: flex;
}

.header .nav.mo_only {
	display: none;
}

.header .nav .menu {
	display: flex;
	align-items: center;
	font-family: "LibreBaskerville", Arial, sans-serif;
	height: 100%;
}

.header .nav .menu > li {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 120px;
	height: 100%;
	color: #fff;
	cursor: pointer;
}

.header .nav .menu h2 {
	text-transform: uppercase;
	font-size: 14px;
	opacity: 0.7;
}

.header .nav .sub-menu-list {
	position: absolute;
	left: 0;
	top: 70px;
	background-color: #fff;
}

.header .nav .sub-menu {
	display: none;
	position: absolute;
	padding-top: 20px;
	width: 100%;
	height: 160px;
	top: 70px;
	text-align: center;
	left: 50%;
	transform: translateX(-50%);
	font-size: 12px;
	color: #444;
}

.header .nav .sub-menu > li a {
	display: inline-block;
}

.header .nav .sub-menu > li + li {
	margin-top: 10px;
}

.header .nav.on .sub-menu {
	display: block;
}

.header .nav.on .menu li:hover h2 {
	opacity: 1;
}

.header .nav .menu::before {
	content: "";
	display: none;
	position: absolute;
	left: 0;
	top: 70px;
	width: 100%;
	height: 160px;
	background-color: #e5e7eb;
}

.header .nav.on .menu::before {
	display: block;
}

/* footer */
.footer {
	padding: 50px 0;
	font-family: "LibreBaskerville", "SUIT", sans-serif;
	font-size: 14px;
	text-align: center;
	color: white;
	background: #000;
}

.footer .company-info {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	margin: 0 auto;
	width: 55%;
}

.footer .company-info li {
	margin-bottom: 6px;
}

.footer .company-info li + li {
	margin-left: 20px;
}

.footer .policy-link {
	margin: 14px 0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.footer .policy-link li + li {
	margin-left: 12px;
}

.footer .copyright {
	font-weight: 700;
}

/* visual */
.wrap {
	display: none;
}

.contents {
	padding-top: 70px;
}

.sec-kv {
	margin-bottom: 110px;
	height: 230px;
}

.sec-kv .img-con {
	overflow: hidden;
	position: relative;
	height: 100%;
}

.sec-kv .img-con img {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: auto;
	height: 100%;
}

/* page-tit */
.page-tit {
	margin-bottom: 70px;
	text-align: center;
}

.page-tit h2 {
	margin-bottom: 30px;
	font-family: "NanumMyeongjo", sans-serif;
	font-size: 36px;
	font-weight: 800;
	color: #212121;
}

.page-tit .desc {
	font-size: 16px;
	font-weight: 500;
}

.sub-tit h3 {
	margin-bottom: 12px;
	font-family: "NanumMyeongjo", sans-serif;
	font-size: 28px;
	font-weight: 800;
}

.sub-tit .en {
	font-family: "LibreBaskerville", Arial, sans-serif;
	font-weight: 400;
}

.sub-tit .desc {
	font-size: 18px;
	word-break: keep-all;
}

.sub-tit .desc li + li {
	margin-top: 20px;
}

.sub-tit .tit-l {
	margin-bottom: 50px;
	font-size: 58px;
	font-weight: 500;
}

.sub-tit .tit-s {
	margin-bottom: 20px;
	font-size: 29px;
	font-weight: 700;
}

.txt-con .tit {
	font-family: "LibreBaskerville", Arial, sans-serif;
	font-size: 75px;
}

.txt-con .desc {
	font-size: 18px;
	word-break: keep-all;
}

.txt-con .tit small {
	font-family: "NanumMyeongjo", sans-serif;
	font-size: 29px;
	font-weight: 700;
}

.inner {
	margin: 0 auto;
	width: 1024px;
}

.inner-l {
	margin: 0 auto;
	max-width: 1920px;
	padding: 0 177px;
}

.inner-l.pd-m {
	padding: 0 112px 0 177px;
}

.inner-l.pd-s {
	padding: 0 60px 0 177px;
}

/* btn */
.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
}

.btn-s {
	padding: 0 25px;
	height: 40px;
	font-size: 14px;
}

.btn-m {
	min-width: 330px;
	height: 60px;
	font-size: 16px;
}

.btn-l {
	min-width: 154px;
	padding: 20px 60px;
	border-radius: 10px;
	font-size: 18px;
}

.btn.bg {
	color: #6b503c;
	background: #ddcbb9;
}

.btn.line {
	border: 1px solid #575757;
}

/* motion */
.motion {
	opacity: 0;
	transform: translateY(10%);
	transition: opacity 0.5s, transform 0.7s;
}

.motion.onTrans {
	opacity: 1;
	transform: translateY(0);
}

.motion.opacity {
	opacity: 0;
	transform: translateY(0);
	transition: opacity 0.7s;
}

.motion.opacity.onTrans {
	opacity: 1;
}

.motion.pos {
	transform: translateY(40%);
	transition: opacity 0.5s, transform 0.7s;
}

.motion.pos.onTrans {
	transform: translateY(0);
}

.motion.delay-1 {
	transition-delay: 0.1s;
}
.motion.delay-2 {
	transition-delay: 0.2s;
}
.motion.delay-3 {
	transition-delay: 0.3s;
}
.motion.delay-4 {
	transition-delay: 0.4s;
}
.motion.delay-5 {
	transition-delay: 0.5s;
}
.motion.delay-6 {
	transition-delay: 0.6s;
}
.motion.delay-7 {
	transition-delay: 0.7s;
}
.motion.delay-8 {
	transition-delay: 0.8s;
}
.motion.delay-9 {
	transition-delay: 0.9s;
}

