}
@media (max-width: 980px) {
	.header .nav.pc_only {
		display: none;
	}

	.header .nav.mo_only {
		display: flex;
	}

	.header .util-btn {
		display: flex;
	}

	.header .util-btn .ico-menu a {
		display: inline-block;
		margin-left: 10px;
		width: 44px;
		height: 44px;
	}

	.header .nav {
		position: fixed;
		top: 0;
		left: -100%;
		overflow: hidden auto;
		padding: 0 0 60px 60px;
		width: 71%;
		height: 100%;
		background: #fff;
		z-index: 10;
		transition: left 0.5s;
	}

	.header .nav .menu {
		margin-top: 150px;
		flex-wrap: wrap;
		height: auto;
	}

	.header .nav .menu > li {
		display: block;
		margin-bottom: 30px;
		padding: 20px 0;
		width: 100%;
		height: auto;
		color: #212121;
	}

	.header .nav .menu > li + li {
		margin-left: 0;
	}

	.header .nav .menu h2 {
		position: relative;
		font-size: 40px;
		opacity: 1;
		cursor: pointer;
	}

	.header .nav .menu h2::after {
		content: "";
		position: absolute;
		top: 50%;
		right: 50px;
		transform: translateY(-50%);
		display: inline-block;
		width: 32px;
		height: 32px;
		background: url("/assets/img//common/ico-arrow-down2.png") no-repeat center/100% auto;
	}

	.header .nav .sub {
		position: absolute;
		left: 0;
		top: 70px;
		width: 100%;
		background-color: #fff;
	}

	.header .nav .sub-menu {
		display: none;
		position: static;
		margin: 60px 0 40px;
		height: auto;
		padding-top: 0;
		text-align: left;
		transform: translateX(0);
		font-size: 32px;
		color: #979797;
	}

	.header .nav .sub-menu > li + li {
		margin-top: 20px;
	}

	.header .nav .ico-close.mo_only {
		position: absolute;
		top: 35px;
		right: 30px;
		display: block;
		width: 65px;
		height: 65px;
		background: url("/assets/img/common/ico-close2.png") no-repeat center/100% auto;
		cursor: pointer;
	}

	.header.on::before {
		content: "";
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: block;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}

	.header.on .nav {
		left: 0;
	}

	.header.on .nav .menu::before {
		display: none;
	}

	.header .nav .menu > li.on h2 {
		border-bottom: 1px solid #212121;
	}

	.header .nav .menu > li.on h2::after {
		background-image: url("/assets/img/common/ico-arrow-up2.png");
	}

	.inner {
		padding: 0 40px;
	}
}
